<template>
  <div>
    <li class="nav-item dropdown">
      <a
        class="nav-link dropdown-toggle"
        href="#"
        id="cart"
        role="button"
        data-toggle="dropdown"
        aria-haspopup="true"
        aria-expanded="false"
      >
        <img
          src="../../assets/cart.png"
          width="32"
          height="32"
          class="img-fluid rounded-circle"
        />

        <span class="badge badge-warning" v-show="itemsInCart > 0">{{
          itemsInCart
        }}</span>
      </a>
      <div class="dropdown-menu cart" aria-labelledby="navbarDropdown">
        <div v-if="!itemsInCart > 0">
          <h6 class="alert alert-danger">No Items Added Yet</h6>
        </div>

        <div v-else>
          <div v-for="(item, index) in inCart" :key="index">
            <div class="d-flex justify-content-between align-items-center mb-2">
              <span class="item-name">{{ item.name }}</span>
              <span class="item-price">${{ item.price }}</span>
              <span class="item-qty">{{ item.qty }}</span>
              <div class="review-order">
                <router-link to="/reviewOrder">
                  <button class="btn btn-outline-danger btn-sm">
                    review order
                  </button>
                </router-link>
              </div>
              <div class="delete-icon">
                <img
                  src="../../assets/delete.png"
                  alt="delete"
                  class="img-fluid"
                  width="15px"
                  height="15px"
                  @click="removeItem(item)"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  name: "Cart",
  computed: {
    inCart() {
      return this.$store.getters.inCart;
    },
    itemsInCart() {
      return this.inCart.length;
    },
    showQty() {
      return this.$store.getters.qty;
    }
  },
  methods: {
    removeItem(item) {
      this.$store.dispatch("removeItem", item);
      //console.log("SUCCESS", item);
    }
  }
};
</script>

<style scoped>
.cart {
  width: 310px;
  padding: 5px;
}

a.minus,
a.plus {
  padding: 5px;
  font-size: 18px;
  color: #c00000;
}

a.minus:hover,
a.plus:hover {
  text-decoration: none;
  cursor: pointer;
}
.delete-icon:hover {
  cursor: pointer;
}
span.quantity {
  padding: 5px;
}

input[type="number"] {
  width: 40px;
}
span.item-name {
  width: 50px;
}
</style>
