<template>
  <div class="product-box">
    <div class="card mb-5" @click="toggle">
      <img :src="product.imgSrc" class="card-img-top" :alt="product.name" />
      <h5 class="card-title">{{ product.name | toLowercase }}</h5>
      <div class="card-body" v-if="isHidden">
        <div class="d-flex justify-content-between">
          <h6 class="card-text text-muted">{{ product.category }}</h6>
          <span>{{ "$" + product.price }}</span>
        </div>
        <button class="btn btn-success btn-block" @click="addToCart(product)">
          Add to cart
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Product",
  props: {
    product: {
      type: Object
    }
  },
  data() {
    return {
      isHidden: false
    };
  },
  methods: {
    toggle() {
      this.isHidden = !this.isHidden;
    },
    addToCart(product) {
      this.$store.dispatch("addToCart", product);
    }
  }
};
</script>

<style scoped>
.product-box img.card-img-top {
  width: 180px;
  height: 250px;
  margin: 10px auto;
}
</style>
