import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

Vue.config.productionTip = false;

Vue.filter("toLowercase", value => {
  return value.toLowerCase();
});

// Import bootsrap
import "bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
