import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    products: [
      {
        id: 1,
        name: "Realme",
        qty: null,
        category: "smart-phones",
        price: 3500,
        imgSrc: require("../assets/mobile.jpg")
      },
      {
        id: 2,
        name: "LG",
        qty: 1,
        category: "Watch TV",
        price: 5995,
        imgSrc: require("../assets/mobile3.jpg")
      },
      {
        id: 3,
        name: "Samsung",
        qty: 1,
        category: "Cameras",
        price: 2595,
        imgSrc: require("../assets/mobile.jpg")
      },
      {
        id: 4,
        name: "OPPO",
        qty: 1,
        category: "smart-phones",
        price: 3500,
        imgSrc: require("../assets/mobile3.jpg")
      }
    ],
    inCart: []
  },
  getters: {
    products: state => state.products,
    inCart: state => state.inCart,
    saleProducts: state => {
      let saleProducts = state.products.map(product => {
        return {
          name: "SALE** " + product.name,
          category: product.category,
          price: product.price / 2,
          imgSrc: product.imgSrc
        };
      });
      return saleProducts;
    }
  },
  mutations: {
    ADD_TO_CART(state, product) {
      if (state.inCart.length) {
        let findProduct = state.inCart.find(x => x.id === product.id);
        if (findProduct) {
          findProduct.qty += 1;
          return;
        }
      }
      state.inCart.push(product);
    },
    REMOVE_FROM_CART: (state, product) => {
      state.inCart.splice(product, 1);
    }
  },
  actions: {
    addToCart(context, product) {
      context.commit("ADD_TO_CART", product);
    },
    removeItem(context, item) {
      context.commit("REMOVE_FROM_CART", item);
    }
  },
  modules: {}
});
