<template>
  <div>
    <section class="form-search mt-4">
      <div class="container">
        <div class="row">
          <div class="col-sm-6 offset-sm-3">
            <input
              type="text"
              class="form-control"
              v-model="search"
              placeholder="Search..."
            />
          </div>
        </div>
      </div>
    </section>
    <section class="product-list">
      <h1 class="text-center mb-5">Best Sellars</h1>
      <div class="container">
        <div class="row">
          <div
            v-for="product in filteredProducts"
            :key="product.id"
            class="col-md-3"
          >
            <Product :product="product" />
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
//import Product child component
import Product from "@/components/Product";
export default {
  name: "ProductList",
  computed: {
    products() {
      return this.$store.getters.products;
    },
    inCart() {
      return this.$store.getters.inCart;
    },
    saleProducts() {
      return this.$store.getters.saleProducts;
    },
    filteredProducts() {
      return this.$store.getters.products.filter(product =>
        product.name.toLowerCase().match(this.search)
      );
    }
  },
  components: {
    Product
  },
  data() {
    return {
      isHidden: false,
      search: ""
    };
  }
};
</script>

<style scoped>
.product-list {
  margin: 50px 0;
  min-height: 500px;
}
</style>
